.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-50%, 0);
		@include min-xs {
			width: 140px;
		}
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
		.orn-2 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(0, 50%);
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
				transform: translate(0, 40%);
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(50%, 0) scaleX(-1);
		@include min-xs {
			width: 140px;
		}
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(0, 50%);
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
				transform: translate(0, 40%);
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 25%;
		left: 0;
		width: 130%;
		transform: translate(-30%, 0);
		@include min-lg {
			top: 40%;
			width: 50%;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 130%;
		transform: translate(30%, 0) scaleX(-1);
		@include min-md {
			transform: translate(30%, 25%) scaleX(-1);
		}
		@include min-lg {
			width: 50%;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.stream-wrapper {
		position: relative;
		z-index: 5;
		@include max-lg {
			.row {
				align-items: center;
				gap: 2rem;
			}
		}
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				background: var(--color-primary);
				color: var(--color-light);
				border-radius: 12px 12px 0 0;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
