.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: fit-content;
		position: relative;
		margin-left: auto;
		margin-right: auto;
		z-index: 5;
		@include max-md {
			margin: 3rem auto;
		}
		span {
			position: relative;
			font-size: 28px;
			font-family: $font-secondary;
			@include max-md {
				font-size: 24px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.couple-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 0 0 5rem;
		@include max-md {
			padding: 0 0 3rem;
		}
		.unicorn-frame {
			position: relative;
			width: 60px;
			margin: 0 auto 2rem;
		}
		.orn-1 {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 150%;
			transform: translate(-50%, -50%);
		}
		.orn-2 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 150%;
			transform: translate(-50%, -30%);
			@include max-md {
				width: 180%;
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-60%, -40%);
			z-index: 3;
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 280px;
			}
			.image-element,
			img {
				transform-origin: 25% 100%;
			}
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 160px;
			transform: translate(60%, -40%) scaleX(-1);
			z-index: 3;
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 280px;
			}
			.image-element,
			img {
				transform-origin: 25% 100%;
			}
		}
		.orn-5 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(-20%, -5%);
			@include min-xs {
				width: 160px;
			}
			@include min-md {
				width: 180px;
			}
			.image-element,
			img {
				transform-origin: 25% 100%;
			}
			.orn-6 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 120px;
				transform: translate(0, 35%);
				@include min-xs {
					width: 140px;
				}
				.image-element,
				img {
					transform-origin: 25% 25%;
				}
			}
		}
		.orn-7 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(20%, -5%) scaleX(-1);
			@include min-xs {
				width: 160px;
			}
			@include min-md {
				width: 180px;
			}
			.image-element,
			img {
				transform-origin: 25% 100%;
			}
			.orn-8 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 120px;
				transform: translate(0, 35%);
				@include min-xs {
					width: 140px;
				}
				.image-element,
				img {
					transform-origin: 25% 25%;
				}
			}
		}
		.orn-9 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: translate(-20%, 0) scaleY(-1);
			z-index: 3;
			@include min-xs {
				width: 140px;
			}
			@include min-md {
				width: 160px;
			}
			.image-element,
			img {
				transform-origin: 25% 25%;
			}
			.orn-10 {
				position: absolute;
				top: 0;
				left: 0;
				width: 120px;
				transform: scaleY(-1);
				@include min-xs {
					width: 140px;
				}
				.image-element,
				img {
					transform-origin: 25% 100%;
				}
			}
		}
		.orn-11 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120px;
			transform: translate(20%, 0) scale(-1);
			z-index: 3;
			@include min-xs {
				width: 140px;
			}
			@include min-md {
				width: 160px;
			}
			.image-element,
			img {
				transform-origin: 25% 25%;
			}
			.orn-12 {
				position: absolute;
				top: 0;
				left: 0;
				width: 120px;
				transform: scaleY(-1);
				@include min-xs {
					width: 140px;
				}
				.image-element,
				img {
					transform-origin: 25% 100%;
				}
			}
		}
	}
	.couple-body {
		position: relative;
		@include max-md {
			.container {
				padding: 0;
			}
		}
	}
	.couple-content {
		position: relative;
		margin-top: 5rem;
		margin-bottom: 3rem;
		@include max-md {
			margin-top: 3rem;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		background: var(--color-primary);
		border-radius: 500px;
		padding: 6rem 3rem 3rem;
		@include max-md {
			padding: 5rem 3rem 3rem;
		}
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 3/4;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 500px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem;
			color: var(--color-light);
			z-index: 5;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
