.thank-section {
	position: relative;
	text-align: center;
	.unicorn-frame {
		position: relative;
		width: 60px;
		margin: 0 auto 2rem;
	}
	.orn-99 {
		position: relative;
		width: 100%;
	}
	.orn-1 {
		position: absolute;
		top: 10%;
		left: 0;
		width: 140px;
		transform: translate(-20%, 0);
		z-index: 3;
		@include min-xs {
			width: 160px;
		}
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 220px;
		}
		.image-element,
		img {
			transform-origin: 25% 25%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 10%;
		right: 0;
		width: 140px;
		transform: translate(20%, 0) scaleX(-1);
		z-index: 3;
		@include min-xs {
			width: 160px;
		}
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 220px;
		}
		.image-element,
		img {
			transform-origin: 25% 25%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-50%, -50%);
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(10%, 0);
			@include min-xs {
				width: 160px;
			}
			@include min-md {
				width: 200px;
				transform: translate(5%, 0);
			}
			@include min-lg {
				width: 240px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(50%, -50%) scaleX(-1);
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(10%, 0);
			@include min-xs {
				width: 160px;
			}
			@include min-md {
				width: 200px;
				transform: translate(5%, 0);
			}
			@include min-lg {
				width: 240px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		@include max-md {
			width: 150%;
			left: 50%;
			transform: translateX(-50%);
		}
		.thank-background {
			position: absolute;
			display: inline-block;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 60%;
			height: 50%;
			object-fit: cover;
			border-radius: 50%;
		}
	}
	.thank-wrapper {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-light);
		padding: 0 0 5rem;
		margin-top: -5rem;
		@include max-md {
			padding: 0 0 3rem;
			margin-top: -8rem;
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
