.rsvp-section {
	position: relative;
	background-color: var(--color-primary);
	color: var(--color-light);
	padding: 5rem 0 0;
	z-index: 3;
	@include max-md {
		padding: 3rem 0 0;
	}
	.elements-wrapper-absolute {
		top: unset;
		bottom: 0;
		left: 50%;
		width: 80%;
		transform: translateX(-50%);
	}
	.rsvp-wrapper {
		position: relative;
		.rsvp-form {
			position: relative;
			margin-bottom: -1.25rem;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
