.venue-section {
	position: relative;
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.venue-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.orn-1 {
		position: relative;
		width: 100%;
		height: auto;
		.frame {
			z-index: 5;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-20%, 30%) scaleX(-1);
		@include min-xs {
			width: 200px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(20%, 30%);
		@include min-xs {
			width: 200px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, -90%) scaleX(-1);
		@include min-xs {
			width: 200px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, -90%);
		@include min-xs {
			width: 200px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 2rem;
		left: 0;
		width: 140px;
		transform: translate(-20%, 0) scaleY(-1);
		z-index: 5;
		@include min-xs {
			width: 160px;
		}
		.image-element,
		img {
			transform-origin: 25% 25%;
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: scaleY(-1);
			@include min-xs {
				width: 140px;
			}
			.image-element,
			img {
				transform-origin: 25% 100%;
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 2rem;
		right: 0;
		width: 140px;
		transform: translate(20%, 0) scale(-1);
		z-index: 5;
		@include min-xs {
			width: 160px;
		}
		.image-element,
		img {
			transform-origin: 25% 25%;
		}
		.orn-9 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: scaleY(-1);
			@include min-xs {
				width: 140px;
			}
			.image-element,
			img {
				transform-origin: 25% 100%;
			}
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		transform: translate(-45%, 55%);
		@include min-xs {
			width: 200px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translate(45%, 55%) scaleX(-1);
		@include min-xs {
			width: 200px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: -6rem auto 5rem;
			padding: 3rem 2rem;
			background: var(--color-primary);
			color: var(--color-light);
			z-index: 2;
			text-align: center;
			border-radius: var(--border-radius);
			@include max-lg {
				margin-bottom: 3rem;
			}
			p {
				margin-bottom: 0;
			}
		}
		.venue-content {
			position: relative;
			padding: 0 0 5rem;
			@include max-md {
				padding: 0 0 3rem;
				.container {
					padding: 0;
				}
			}
			.ribbon-venue {
				position: relative;
				margin: 1rem 0 0.75rem;
				svg {
					width: 40px;
					height: auto;
					.st0 {
						fill: var(--color-light);
					}
					.st1 {
						stroke: var(--color-light);
					}
				}
			}
			.card {
				background-color: transparent;
				height: 100%;
				z-index: 5;
				text-align: center;
				border: none;
				color: var(--color-light);
				.card-body {
					position: relative;
					padding: 0;
					h4 {
						margin: 0 0 1.25rem;
					}
					hr {
						display: block;
						width: 15%;
						height: 2px;
						margin: 0.5rem auto 1rem;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0 3rem;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 5rem;
	}
	.event-name {
		position: relative;
		width: 100%;
		z-index: 3;
		.date {
			position: relative;
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			h5 {
				font-family: $font-secondary;
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
				min-width: 30%;
			}
		}
		h4 {
			font-family: $font-secondary;
			color: var(--color-button-trans);
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
		.event-details {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 60%;
			height: 50%;
			transform: translate(-50%, -43%);
			background-color: var(--color-primary);
			border-radius: 50%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			z-index: 3;
		}
	}
	.event-place {
		position: relative;
		padding: 10rem 2rem 3rem;
		margin-top: -10rem;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		p {
			margin-bottom: 0;
		}
		&.no-event {
			padding: 3rem 2rem;
			margin-top: 0;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
		.widget-elements {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 50%);
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
