.reminder-wrap {
	position: relative;
	display: block;
	padding: 10rem 0 15rem;
	@include max-md {
		padding: 13rem 0 15rem;
	}
	.orn-99 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 250%;
		transform: translate(-50%, -50%);
	}
	.orn-1 {
		position: absolute;
		top: 3rem;
		left: 0;
		width: 180px;
		transform: translate(-30%);
		z-index: 3;
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 3rem;
		right: 0;
		width: 180px;
		transform: translate(30%, 0) scaleX(-1);
		z-index: 3;
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-30%, 0);
		z-index: 3;
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 120px;
			transform: translate(-20%, -65%) scaleY(-1);
			@include min-xs {
				width: 140px;
			}
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			.image-element,
			img {
				transform-origin: 25% 25%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(30%, 0) scaleX(-1);
		z-index: 3;
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120px;
			transform: translate(-20%, -90%) scaleY(-1);
			@include min-xs {
				width: 140px;
			}
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			.image-element,
			img {
				transform-origin: 25% 25%;
			}
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		padding: 5rem 1rem;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
