.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-99 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 150%;
		transform: translate(-50%, -50%);
		@include max-lg {
			width: 180%;
		}
		@include max-md {
			width: 200%;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0);
		z-index: 3;
		@include min-xs {
			top: 3%;
			width: 200px;
		}
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		.image-element,
		img {
			transform-origin: 10% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(40%, 0) scaleX(-1);
		z-index: 3;
		@include min-xs {
			top: 3%;
			width: 200px;
		}
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		.image-element,
		img {
			transform-origin: 10% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-20%, 20%);
		z-index: 3;
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		img {
			transform-origin: 30% 80%;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 180px;
			transform: translate(0, 20%);
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			img,
			.image-element {
				transform-origin: 20% 100%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(20%, 20%) scaleX(-1);
		z-index: 3;
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		img {
			transform-origin: 30% 80%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 180px;
			transform: translate(0, 20%);
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			img,
			.image-element {
				transform-origin: 20% 100%;
			}
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 3rem 2rem;
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.cover-description {
					position: relative;
					padding: 0 2rem;
					text-align: center;
					z-index: 5;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					.title {
						margin: 1.5rem 0;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
