.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-10%, 0);
		@include min-xs {
			width: 160px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: translate(10%, 0);
			@include min-xs {
				width: 140px;
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100px;
			transform: translate(-10%, 50%);
			@include min-xs {
				width: 120px;
			}
			.image-element,
			img {
				transform-origin: 25% 25%;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(10%, 0) scaleX(-1);
		@include min-xs {
			width: 160px;
		}
		.image-element,
		img {
			transform-origin: 25% 100%;
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: translate(10%, 0);
			@include min-xs {
				width: 140px;
			}
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100px;
			transform: translate(-10%, 50%);
			@include min-xs {
				width: 120px;
			}
			.image-element,
			img {
				transform-origin: 25% 25%;
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 5rem 2rem 3rem;
		background: var(--color-primary);
		color: var(--color-light);
		border-radius: 500px 500px 0 0;
		@include max-md {
			padding: 6rem 1rem 2rem;
		}
	}
}
