.header-section {
	position: relative;
	overflow: hidden;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
		rect {
			stroke: var(--color-light);
		}
	}
	.orn-99 {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 170%;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-20%, -20%);
		z-index: 3;
		@include min-xs {
			width: 180px;
		}
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 25% 25%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(20%, -20%) scaleX(-1);
		z-index: 3;
		@include min-xs {
			width: 180px;
		}
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 25% 25%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-30%, 30%);
		z-index: 3;
		@include min-xs {
			width: 220px;
		}
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 260px;
		}
		img {
			transform-origin: 30% 80%;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 180px;
			transform: translate(10%, 10%);
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 260px;
			}
			img,
			.image-element {
				transform-origin: 25% 100%;
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(0, 10%) scaleX(-1);
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 240px;
				transform: translate(20%, 15%) scaleX(-1);
			}
			@include min-lg {
				width: 260px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(30%, 30%) scaleX(-1);
		z-index: 3;
		@include min-xs {
			width: 220px;
		}
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 260px;
		}
		img {
			transform-origin: 30% 80%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 180px;
			transform: translate(10%, 10%);
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			img,
			.image-element {
				transform-origin: 25% 100%;
			}
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(0, 10%);
			@include min-xs {
				width: 200px;
			}
			@include min-md {
				width: 240px;
				transform: translate(20%, 15%);
			}
			@include min-lg {
				width: 260px;
			}
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 35%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 4rem;
			color: var(--color-light);
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 3rem 2rem;
				width: 100%;
				min-height: 85%;
				text-align: center;
				@include max-lg {
					min-height: 78%;
				}
				@include max-md {
					min-height: 65%;
				}
				@include max-xs {
					min-height: 75%;
				}
				border-radius: 50%;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				&::after {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: rgba($color: #000000, $alpha: 0.3);
					border-radius: 50%;
					content: "";
				}
				.header-background {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: 50%;
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
